import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import useModals from '../components/hooks/useModals';
import { modals } from '../components/modal/Modals';

import useTabs from '../components/hooks/useTabs';

import { mainSlideData, roomData, newsData } from '../assets/js/Data';

import { Autoplay, Navigation, Scrollbar } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

function Main() {
	// tourApi
	// { type: '10', name: '( )', contentId: 127590 },
	// { type: '/', name: '', contentId: 126265 },
	const key = 'GRG3PBRleN3OEAUciihOp4A2GcHkx%2BdVgI9Mii%2FvyPhTh2y2o5kQ0s6%2F11TdJ5nc8UH9Je8yttDUCe4o7lAcCw%3D%3D';
	const [aroundContent, setAroundContent] = useState([]);
	const getMovie = async (key, curContentId) => {
		const json1 = await (
			await fetch(
				`https://apis.data.go.kr/B551011/KorService1/detailCommon1?serviceKey=${key}&MobileOS=ETC&MobileApp=%EB%B4%89%EC%A5%AC%EB%A5%B4%ED%8E%9C%EC%85%98&_type=json&contentId=127590&defaultYN=Y&firstImageYN=Y&areacodeYN=Y&catcodeYN=Y&addrinfoYN=Y&mapinfoYN=Y&overviewYN=Y`,
			)
		).json();
		const json2 = await (
			await fetch(
				`https://apis.data.go.kr/B551011/KorService1/detailCommon1?serviceKey=${key}&MobileOS=ETC&MobileApp=%EB%B4%89%EC%A5%AC%EB%A5%B4%ED%8E%9C%EC%85%98&_type=json&contentId=126265&defaultYN=Y&firstImageYN=Y&areacodeYN=Y&catcodeYN=Y&addrinfoYN=Y&mapinfoYN=Y&overviewYN=Y`,
			)
		).json();
		setAroundContent([json1.response.body.items.item[0], json2.response.body.items.item[0]]);
	};
	useEffect(() => {
		getMovie(key);
	}, [key]);

	// 
	const { openModal } = useModals();
	const handleClick = (e) => {
		openModal(modals.News, {
			dataId: Number(e.currentTarget.dataset.id),
		});
	};

	// 
	const [swiper, setSwiper] = useState(null);
	const [slideNum, setSlideNum] = useState(0);
	const swiperParams = {
		modules: [Autoplay, Navigation, Scrollbar],
		onSwiper: setSwiper,
		onSlideChange: () => {
			setSlideNum(swiper.activeIndex);
		},
		scrollbar: {
			draggable: true,
		},
		navigation: true,
		grabCursor: true,
		autoplay: {
			delay: 6500,
			disableOnInteraction: false,
		},
		speed: 500,
	};

	//   
	const { currentItem, changeItem } = useTabs(0, roomData);

	//  
	const [imgNum, setImgNum] = useState(0);
	const nextImg = () => {
		if (imgNum >= currentItem.images.length - 1) {
			setImgNum(0);
		} else {
			setImgNum((prev) => prev + 1);
		}
	};
	const prevImg = () => {
		if (imgNum <= 0) {
			setImgNum(currentItem.images.length - 1);
		} else {
			setImgNum((prev) => prev - 1);
		}
	};

	//  html 
	const stringToHTML = function (str) {
		var dom = document.createElement('div');
		dom.innerHTML = str;
		return dom;
	};

	return (
		<div className="content main">
			{/* ==============================   ============================== */}
			<div className="main-slide slide-figure">
				<Swiper {...swiperParams}>
					{mainSlideData.map((slide, i) => (
						<SwiperSlide key={i}>
							<figure className="auto-height-figure">
								<img src={`${process.env.PUBLIC_URL}/img/mainslide/summer/${i}.jpg`} alt="" />
								<figcaption>{slide.caption}</figcaption>
							</figure>
						</SwiperSlide>
					))}
				</Swiper>
			</div>
			<div className="visual-text">
				<div>
					<h2 className="title">
						  
						<br />  
						<br />  
					</h2>
					<p className="h3 sub-title">{mainSlideData[slideNum].subTitle}</p>
					<p className="text">
						     ,                    .
					</p>
					<p className="text">
						 <b>010-8746-1409</b> .  .       . .
					</p>
				</div>
				<Link className="btn-viewall designed-border-1" to="about">
					<i className="border-top"></i>
					<div className="border-inner">
						<div className="pb">
							<i className="logo"></i>
							<div>
								<p>Pension Bonjour</p>
								<h1 className="h4"> </h1>
							</div>
						</div>
						<span> </span>
					</div>
					<i className="border-bottom"></i>
				</Link>
			</div>
			{/* ==============================     ============================== */}
			{/* ==============================   ============================== */}
			<div className="rooms-tit">
				<div className="title with-viewmore">
					<h2></h2>
					<Link to="/rooms" className="h4">
						
					</Link>
				</div>
				<div className="tab-wrap">
					<div className="tab-title">
						{roomData.map((room) => (
							<button
								key={room.id}
								className={currentItem.id === room.id ? 'h3 active' : 'h3'}
								onClick={() => {
									changeItem(room.id);
									setImgNum(0);
								}}
							>
								<span className="en">{room.en}</span>
								<span className="ko">{room.ko}</span>
							</button>
						))}
					</div>
				</div>
			</div>
			<div className="rooms-figure">
				{currentItem.images.map((img, i) => (
					<figure className={imgNum === i ? 'auto-height-figure active' : 'auto-height-figure'} key={i}>
						<img src={`${process.env.PUBLIC_URL}/img/room/${img}.jpg`} alt="" />
						<figcaption>{currentItem.ko}     </figcaption>
					</figure>
				))}
			</div>
			<div className="rooms-text">
				<div>
					<h3></h3>
					<p className="text">{currentItem.info}</p>
				</div>
				<div>
					<h4></h4>
					<p className="text">{currentItem.personnel}</p>
				</div>
				<div>
					<h4></h4>
					<p className="text">{currentItem.config}</p>
				</div>
				<div>
					<h4></h4>
					<p className="text">{currentItem.bed}</p>
				</div>
				<div>
					<h4></h4>
					<p className="text">{currentItem.size}</p>
				</div>
				<div className="control">
					<button onClick={prevImg}>&lt;</button>
					<span>
						{imgNum + 1} / {currentItem.images.length}
					</span>
					<button onClick={nextImg}>&gt;</button>
				</div>
			</div>
			{/* ==============================     ============================== */}
			{/* ============================== (, )  ============================== */}
			<div className="info auto-height">
				<div className="info-waytocome">
					<div className="title with-viewmore">
						<h2></h2>
						<Link to="/about" className="h4">
							
						</Link>
					</div>
					<i className="img">
						<img src={`${process.env.PUBLIC_URL}/img/map.jpg`} alt="" />
					</i>
					<p className="h4 text">    4310-20 </p>
					<p className="text">        .</p>
				</div>
				<button className="auto-height-component designed-border-2">
					<i className="border-top"></i>
					<div className="border-inner h4"></div>
					<i className="border-bottom"></i>
				</button>
				<div className="info-reservation">
					<div className="title with-viewmore">
						<h2></h2>
						<Link to="/reservation" className="h4">
							
						</Link>
					</div>
					<table>
						<colgroup>
							<col width="80px" />
						</colgroup>
						<tbody>
							<tr>
								<th></th>
								<td> 15:00</td>
							</tr>
							<tr>
								<th></th>
								<td> 11:00</td>
							</tr>
							<tr>
								<th> </th>
								<td>
									<ul className="list-style">
										<li className="bul">,  20,000</li>
										<li className="bul"> 10,000</li>
										<li className="bul"> 5kg 10,000, 5~20kg 20,000, 20kg  30,000</li>
									</ul>
								</td>
							</tr>
							<tr>
								<th> </th>
								<td>
									<ul className="list-style">
										<li className="bul">
											 20,000
											<ul>
												<li className="star">,   5,000</li>
											</ul>
										</li>
									</ul>
								</td>
							</tr>
							<tr>
								<th> </th>
								<td>20,000</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
			{/* ============================== (, )    ============================== */}
			{/* ==============================   ============================== */}
			<div className="around">
				<div className="title with-viewmore">
					<h2></h2>
					<Link to="/around" className="h4">
						
					</Link>
				</div>
				<ul className="around-list">
					{aroundContent.map((around, i) => (
						<li key={around.contentid}>
							<figure>
								<img src={around.firstimage} alt="" />
								<figcaption>{around.title} </figcaption>
							</figure>
							<div className="around-text">
								<h4>{around.title}</h4>
								<p className="text">{around.overview}</p>
								<p className="source">  : </p>
							</div>
						</li>
					))}
				</ul>
			</div>
			{/* ==============================     ============================== */}
			{/* ==============================   ============================== */}
			<div className="faq">
				<div className="title with-viewmore">
					<h2>FAQ</h2>
					<Link to="/faq" className="h4">
						
					</Link>
				</div>
				<div className="auto-height">
					<div className="auto-height-content">
						<h4 className="sub-title">     .</h4>
						<p className="text">
							     <b>010-8746-1409</b> .  . .
						</p>
					</div>
					<button className="auto-height-component designed-border-2">
						<i className="border-top"></i>
						<a href="tel:010-8746-1409" className="border-inner h4">
							 
						</a>
						<i className="border-bottom"></i>
					</button>
				</div>
			</div>
			{/* ==============================     ============================== */}
			{/* ==============================   ============================== */}
			<div className="news">
				{[newsData[0], newsData[1], newsData[2]].map((news) => {
					return (
						<button onClick={handleClick} key={news.id} data-id={news.id}>
							<div className="thumb">
								<img src={`${process.env.PUBLIC_URL}/img/news/${news.id}/0.jpg`} alt="" />
							</div>
							<div className="cont">
								<h4 className="tit">{news.title}</h4>
								<p className="text" dangerouslySetInnerHTML={{ __html: stringToHTML(news.html).innerText }}></p>
							</div>
						</button>
					);
				})}
				{/* ==============================     ============================== */}
			</div>
		</div>
	);
}

export default Main;
