import ActiveLink from './ActiveLink';

function Gnb({ className, menuClick }) {
	return (
		<nav className={className}>
			<ul onClick={menuClick}>
				<li>
					<ActiveLink path="about" ko="" />
				</li>
				<li>
					<ActiveLink path="news" ko="" />
				</li>
				<li>
					<ActiveLink path="rooms" ko="" />
				</li>
				<li>
					<ActiveLink path="reservation" ko="" />
				</li>
				<li>
					<ActiveLink path="around" ko="" />
				</li>
				<li>
					<ActiveLink path="faq" ko="" />
				</li>
			</ul>
		</nav>
	);
}

export default Gnb;
